@media (max-width: 768px) {
  .App > .main-wrapper {
    min-height: calc(100vh - 155px);
  }
}

@media (max-width: 475px) {
  .App > .main-wrapper {
    min-height: calc(100vh - 199px);
  }
}

.main-wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  min-height: calc(100vh - 106px);
}

.main__title {
  margin: 8px;
  font-size: 22px;
  text-align: center;
}
